import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const AdWrapper = styled.div`
  position: relative;
  width: 100%;
  text-align: center;
  margin: 10px 0;
`;

const AdIframe = styled.iframe`
  max-width: 100%;
  border: none;
  display: inline-block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
  position: absolute;
`;

const AdPanel = ({ src, style }) => {
  return (
    <AdWrapper style={{ height: style.height, minWidth: style.minWidth }}>
      <AdIframe src={src} style={style} />
    </AdWrapper>
  );
};

export default AdPanel;

AdPanel.propTypes = {
  src: PropTypes.string.isRequired,
  style: PropTypes.object,
};

AdPanel.defaultProps = {
  style: {
    width: '300px',
    height: '100px',
  },
};
