import React from 'react';
import styled from 'styled-components';
import logo from '../images/logo.png';

const Container = styled.div`
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: #71b38f;
  z-index: 9999;
  opacity: 1;
  -webkit-transition: 0.5s linear;
  -o-transition: 0.5s linear;
  transition: 0.5s linear;
`;

const LoadingLogo = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  margin: 0 auto;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  img {
    width: 100%;
  }
`;

const SubTitle = styled.div`
  color: #fff;
  text-align: center;
  font-size: 10px;
  margin-top: 10px;
  font-weight: 600;
`;

const Dots = styled.ul`
  position: absolute;
  top: 120%;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  font-size: 0;
  list-style: none;
  padding-left: 0;
  margin-top: 10px;

  li {
    height: 0;
    position: absolute;
    top: 50%;
    left: 0;
    width: 0;
    margin: 0;
    height: 5px;
    width: 5px;
    border: 3px solid #fff;
    border-radius: 100%;
    -webkit-transform: transformZ(0);
    -ms-transform: transformZ(0);
    transform: transformZ(0);
    -webkit-animation: loading-dot 2s infinite;
    animation: loading-dot 2s infinite;

    &:first-child {
      left: -20px;
      -webkit-animation-delay: 0s;
      animation-delay: 0s;
    }

    &:nth-child(2) {
      left: 0;
      -webkit-animation-delay: 0.2s;
      animation-delay: 0.2s;
    }

    &:nth-child(3) {
      left: 20px;
      -webkit-animation-delay: 0.4s;
      animation-delay: 0.4s;
    }
  }
`;

const Loading = () => (
  <Container>
    <LoadingLogo>
      <img alt="nookstamp-logo" src={logo} />
      <SubTitle>Share with friends</SubTitle>
      <Dots>
        <li></li>
        <li></li>
        <li></li>
      </Dots>
    </LoadingLogo>
  </Container>
);

export default Loading;
