import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import debounce from 'debounce';
import { useTranslation } from 'react-i18next';
import { Select, Dropdown } from 'element-react';
import logo from '../images/logo.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch, faArchive } from '@fortawesome/free-solid-svg-icons';
import {
  faQuestionCircle,
  faPaperPlane,
  faSave,
  faStickyNote,
} from '@fortawesome/free-regular-svg-icons';
import background from '../images/background.png';
import { TOKEN_KEY } from '../constants';

const MainTop = styled.div`
  background-color: transparent;
`;

const Background = styled.div`
  position: absolute;
  z-index: -1;
  background-color: #a8ce9d;
  background-image: url(${background});
  height: 150px;
  border-radius: 0 0 0 70px;
  width: 100%;

  @media (max-width: 485px) {
    height: 140px;
  }

  @media (max-width: 320px) {
    height: 105px;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    height: 55px;
    top: 0;
    border-radius: 0 0 0 20px;
  }
`;

const Panel = styled.div`
  height: 100px;
  line-height: 100px;
  display: flex;

  @media (max-width: 512px) {
    justify-content: center;
  }

  @media (max-width: 320px) {
    height: 70px;
    line-height: 70px;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    height: 70px;
    line-height: 70px;
    display: none;
  }

  img {
    width: 250px;
    height: auto;
    align-self: center;
    margin: 0 25px;
  }
`;

const CountWrapper = styled.div`
  height: 50px;
  background-color: #fff;
  border-radius: 6px;
`;

const Icon = styled.div`
  color: #a8ce9d;
  font-size: 26px;
  line-height: 50px;
  width: 50px;
  text-align: center;
  display: inline-block;
  vertical-align: top;
`;

const VariationValue = styled.div`
  position: relative;
  background-color: #a8ce9d;
  width: 65px;
  height: 38px;
  color: #fff;
  display: inline-block;
  vertical-align: top;
  margin: 5px;
  border-radius: 6px;

  div:nth-child(1) {
    bottom: 0px;
    right: 4px;
    position: absolute;
    line-height: 16px;
    text-align: center;
    width: 40px;
    font-weight: 600;
    color: #fff;
  }

  div:nth-child(2) {
    top: 2px;
    left: 5px;
    position: absolute;
    width: 20px;
    line-height: 18px;
    text-align: center;
    font-weight: 600;
    color: #000;
    font-size: 24px;
  }
`;

const VariationCount = styled.div`
  z-index: 300;
  align-self: center;

  @media (max-width: 512px) {
    position: fixed;
    right: 2px;
    bottom: 2px;
    height: 36px;

    ${CountWrapper} {
      background-color: #71b38f;
      border-radius: 6px;
      height: 36px;
      position: relative;

      ${Icon} {
        font-size: 20px;
        line-height: 36px;
        width: 38px;
        height: 36px;
        text-align: center;
        display: none;
      }

      ${VariationValue} {
        background-color: transparent;
        font-size: 20px;
        height: 36px;
        margin: 0px;
        width: 60px;

        div:nth-child(1) {
          font-size: 15px;
          line-height: 17px;
          right: 1px;
        }
        div:nth-child(2) {
          font-size: 20px;
          top: 4px;
          left: 3px;
        }
      }
    }

    ${Icon} {
      color: #fff;
    }
  }
`;

const Nav = styled.div`
  height: 120px;

  @media (max-width: 485px) {
    height: 90px;
  }

  @media (max-width: 320px) {
    height: 75px;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    height: 75px;
    position: fixed;
    height: 75px;
    position: fixed;
    top: 60px;
    right: 10px;
    z-index: 200;
  }

  ul {
    padding: 0;
    margin: 0;
    height: 100%;

    li {
      margin: 0;
      padding: 0;
      display: inline-block;
      margin-left: 20px;
      vertical-align: top;
      list-style: none;

      @media screen and (max-width: 1024px) and (orientation: landscape) {
        display: block;
        margin-top: 10px;
      }
    }
  }
`;

const NavButtonWrapper = styled.div`
  cursor: pointer;
  height: 100px;
  width: 100px;
  line-height: 100px;
  background-color: #fffefe;
  box-shadow: 0px 0px 5px 3px rgba(0, 0, 0, 0.08);
  border-radius: 20px;
  position: relative;
  transition: all 0.1s linear;

  @media (max-width: 485px) {
    width: 70px;
    height: 70px;
    line-height: 70px;
  }

  @media (max-width: 320px) {
    width: 60px;
    height: 60px;
    line-height: 60px;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    width: 60px;
    height: 60px;
    line-height: 60px;
  }

  &:hover {
    background-color: #fff;
    box-shadow: 2px 0px 5px 3px rgba(0, 0, 0, 0.18);
  }
`;

const NavButtonIcon = styled.div`
  position: absolute;
  top: 15px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  height: 50px;
  line-height: 50px;

  @media (max-width: 485px) {
    height: 30px;
    line-height: 30px;
    font-size: 30px;
    top: 13px;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    height: 30px;
    line-height: 30px;
    font-size: 30px;
    top: 13px;
  }
`;

const NavButtonTitle = styled.div`
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 15px;
  text-align: center;
  height: 20px;
  width: 80px;
  line-height: 20px;

  @media (max-width: 485px) {
    font-size: 12px;
    width: 50px;
    bottom: 5px;
  }

  @media (max-width: 320px) {
    display: none;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    display: none;
  }
`;

const Search = styled.div`
  display: inline-block;
  height: 34px;
  line-height: 34px;
  width: calc(100% - 160px);
`;

const InputWrapper = styled.div`
  display: inline-block;
  height: 100%;
  position: relative;
  width: 100%;
`;

const Tool = styled.div`
  transition: all 0.1s linear;
  width: 100%;
`;

const SearchIcon = styled.div`
  position: absolute;
  height: 40px;
  line-height: 40px;
  width: 30px;
  top: 0;
  left: 10px;
`;

const Input = styled.input`
  height: 100%;
  width: calc(100% - 30px);
  border: none;
  padding: 0 15px 0 35px;
  box-shadow: none;
  position: absolute;
  left: 0;
  top: 0;
  outline: none;
  border-radius: 4px;
  border: 1px solid #bfcbd9;
  outline: none;
  box-shadow: none;
`;

const StyledSelect = styled(Select)`
  height: 34px;
  margin-left: 50px;

  @media (max-width: 485px) {
    width: 30%;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    width: 30%;
  }
`;

const Filter = styled.div`
  display: flex;
  align-items: center;
  padding: 0 20px;
  justify-content: flex-end;
  height: 40px;

  @media (max-width: 485px) {
    padding: 0 15px;
    justify-content: space-between;
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    padding: 0 15px;
    justify-content: space-between;
    position: fixed;
    width: calc(100% - 30px);
    z-index: 200;
    top: 0;
  }
`;

const StyledDropdown = styled(Dropdown)`
  position: absolute;
  top: 10px;
  right: 20px;
`;

const Header = ({
  total,
  owned,
  shouldShowMyCollection,
  filterType,
  onSaveClick,
  onShareClick,
  onSearchInputChange,
  onFilterTypeChange,
}) => {
  const { t, i18n } = useTranslation();
  const debounceOnSearchInputChange = debounce(
    value => onSearchInputChange(value),
    1000
  );
  let lang;
  switch (i18n.language) {
    case 'en':
      lang = 'English';
      break;
    case 'ja':
      lang = '日本語';
      break;
    case 'zh-TW':
      lang = '繁體中文';
      break;
    default:
      lang = '繁體中文';
  }

  return (
    <MainTop>
      <StyledDropdown
        onCommand={command => {
          i18n.changeLanguage(command);
        }}
        menu={
          <StyledDropdown.Menu>
            <StyledDropdown.Item command="zh-TW">繁體中文</StyledDropdown.Item>
            <StyledDropdown.Item command="en">English</StyledDropdown.Item>
            <StyledDropdown.Item command="ja">日本語</StyledDropdown.Item>
          </StyledDropdown.Menu>
        }
      >
        <span className="el-dropdown-link">
          {lang}
          <i className="el-icon-caret-bottom el-icon--right"></i>
        </span>
      </StyledDropdown>

      <Background />
      <Panel>
        <img src={logo} alt="Nook Stamp Logo" />

        {total !== 0 && (
          <VariationCount>
            <CountWrapper>
              <Icon>
                <FontAwesomeIcon icon={faArchive} />
              </Icon>
              <VariationValue>
                <div>/{total}</div>
                <div>{owned}</div>
              </VariationValue>
            </CountWrapper>
          </VariationCount>
        )}
      </Panel>
      <Tool>
        <Nav>
          <ul>
            {shouldShowMyCollection && (
              <li>
                <Link target="_blank" to="/">
                  <NavButtonWrapper>
                    <NavButtonIcon>
                      <FontAwesomeIcon icon={faStickyNote} />
                    </NavButtonIcon>
                    <NavButtonTitle>
                      {localStorage.getItem(TOKEN_KEY)
                        ? t('tool.backToCollection')
                        : t('tool.createCollection')}
                    </NavButtonTitle>
                  </NavButtonWrapper>
                </Link>
              </li>
            )}
            <li onClick={onSaveClick}>
              <NavButtonWrapper>
                <NavButtonIcon>
                  <FontAwesomeIcon icon={faSave} />
                </NavButtonIcon>
                <NavButtonTitle>{t('tool.saveCollection')}</NavButtonTitle>
              </NavButtonWrapper>
            </li>

            <li onClick={onShareClick}>
              <NavButtonWrapper>
                <NavButtonIcon>
                  <FontAwesomeIcon icon={faPaperPlane} />
                </NavButtonIcon>
                <NavButtonTitle>{t('tool.shareCollection')}</NavButtonTitle>
              </NavButtonWrapper>
            </li>

            <li
              onClick={() => {
                window.open('https://forms.gle/t5AoWM6zzZYYzzeF6');
              }}
            >
              <NavButtonWrapper>
                <NavButtonIcon>
                  <FontAwesomeIcon icon={faQuestionCircle} />
                </NavButtonIcon>
                <NavButtonTitle>{t('tool.report')}</NavButtonTitle>
              </NavButtonWrapper>
            </li>
          </ul>
        </Nav>

        <Filter>
          <Search>
            <InputWrapper>
              <Input
                placeholder={t('search.placeholder')}
                onChange={e => debounceOnSearchInputChange(e.target.value)}
              />
              <SearchIcon>
                <FontAwesomeIcon icon={faSearch} />
              </SearchIcon>
            </InputWrapper>
          </Search>

          <StyledSelect
            value={filterType}
            placeholder={t('select.placeholder')}
            onChange={onFilterTypeChange}
          >
            <StyledSelect.Option
              label={t('select.all')}
              value="all"
              key="all"
            ></StyledSelect.Option>
            <StyledSelect.Option
              label={t('select.owned')}
              value="owned"
              key="owned"
            ></StyledSelect.Option>
            <StyledSelect.Option
              label={t('select.notOwned')}
              value="not-owned"
              key="not-owned"
            ></StyledSelect.Option>
          </StyledSelect>
        </Filter>
      </Tool>
    </MainTop>
  );
};

Header.propTypes = {
  total: PropTypes.number,
  owned: PropTypes.number,
  filterType: PropTypes.oneOf(['all', 'owned', 'not-owned']),
  shouldShowMyCollection: PropTypes.bool,
  onSaveClick: PropTypes.func.isRequired,
  onShareClick: PropTypes.func.isRequired,
  onSearchInputChange: PropTypes.func.isRequired,
  onFilterTypeChange: PropTypes.func.isRequired,
};

Header.defaultProps = {
  total: 0,
  owned: 0,
  filterType: 'all',
  shouldShowMyCollection: false,
};

export default Header;
