import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { AutoSizer, List } from 'react-virtualized';
import { Message, Dialog, Button } from 'element-react';
import { useTranslation } from 'react-i18next';
import 'normalize.css';
import 'element-theme-default';
import 'react-virtualized/styles.css';
import JSONData from './content/output';
import Header from './components/Header';
import Codes from './utils/codes';
import clipboard from './utils/clipboard';
import { TOKEN_KEY } from './constants';
import { sortZhtw, sortEn, sortJa } from './constants/sort';
import Loading from './components/Loading';
import AdPanel from './components/AdPanel';
import Footer from './components/Footer';

const Row = styled.div`
  padding-top: 12px;
  border-top: 1px solid rgba(113, 178, 142, 0.3);
`;

const Col = styled.div`
  display: inline-block;
  font-weight: bolder;
  font-size: 15px;
  width: 50px;
  vertical-align: top;
  height: 100%;
  line-height: 50px;
  text-align: center;
`;

const Name = styled.div`
  display: inline-block;
  font-weight: bolder;
  font-size: 15px;
  width: 120px;
  overflow: hidden;
  vertical-align: top;
  height: 50px;

  div:nth-child(2) {
    font-size: 13px;
    color: #bbb;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
`;

const Variations = styled.div`
  display: inline-block;
  width: calc(100% - 170px);
  white-space: nowrap;
  vertical-align: top;
  height: 50px;
`;

const VariationToolTip = styled.div`
  visibility: hidden;
  width: 120px;
  height: 20px;
  line-height: 20px;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: -30px;
  left: 50%;
  margin-left: -60px;
  opacity: 0;
  transition: opacity 0.2s;
  font-size: 12px;

  &::after {
    content: '';
    position: absolute;
    top: -10px;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
    transform: rotate(180deg);
  }
`;

const VariationContainer = styled.div`
  position: relative;
  display: inline-block;

  &:hover ${VariationToolTip} {
    visibility: visible;
    opacity: 1;
  }
`;

const VariationImg = styled.img`
  margin-right: 3px;
  border-radius: 4px;
  opacity: 0.8;
  cursor: pointer;
  display: inline-block;
  width: 50px;
  height: 50px;

  ${({ isOwned }) =>
    isOwned &&
    css`
      background-color: #40474e;
      color: #fff;
      opacity: 1;
    `}
`;

const ShareBox = styled.div`
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 1px 2px 0 rgba(0, 0, 0, 0.24);
  cursor: pointer;
  border-radius: 3px;
  padding: 20px;
  margin: 10px 0;
  background-color: #f9fbfb;
`;

const DialogFooter = styled.div`
  padding: 0 20px 30px;
  text-align: right;
`;

const ScrollContainer = styled.div`
  padding: 20px 15px 0;
  height: calc(100vh - 340px);

  @media (max-width: 485px) {
    padding: 10px 15px 0;
    height: calc(100vh - 280px);
  }

  @media (max-width: 320px) {
    height: calc(100vh - 235px);
  }

  @media screen and (max-width: 1024px) and (orientation: landscape) {
    margin-top: 50px;
    height: calc(100vh - 100px);
  }
`;

const EmptyResult = styled.div`
  text-align: center;
  color: #bbb;
  line-height: 30px;
  border: solid 1px #bbb;
  border-radius: 3px;
  white-space: pre-wrap;
`;

const App = props => {
  const { results, total_variation: totalVariation } = JSONData;
  const { t, i18n } = useTranslation();

  const [loading, setLoading] = useState(true);
  const [data, setData] = useState(results);
  const [status, setStatus] = useState(new Array(totalVariation).fill(0));
  const [shareUrl, setShareUrl] = useState('');
  const [isShareDialogVisible, setIsShareDialogVisible] = useState(false);
  const [filterType, setFilterType] = useState('all');
  const [term, setTerm] = useState('');

  const onVariationClick = idx => {
    const cloneStatus = [...status];
    cloneStatus[idx] = status[idx] ? 0 : 1;
    setStatus(cloneStatus);
  };

  const onLanguageChange = lang => {
    i18n.changeLanguage(lang);
  };

  const onSaveClick = () => {
    const hash = Codes.generateHash(status.join(''));
    localStorage.setItem(TOKEN_KEY, hash);
    Message({
      message: t('tool.saveSuccess'),
      type: 'success',
    });
  };

  const onShareClick = () => {
    Codes.generateURL(status.join(''), url => {
      setIsShareDialogVisible(true);
      setShareUrl(url);
    });
  };

  const onSearchInputChange = term => {
    setTerm(term);
  };

  const onFilterTypeChange = filterType => {
    setFilterType(filterType);
  };

  const handleClipboard = (text, event) => {
    const success = () => {
      Message({
        message: t('tool.copySuccess'),
        type: 'success',
      });

      setIsShareDialogVisible(false);
    };

    const error = () => {
      Message({
        message: t('tool.copyFail'),
        type: 'error',
      });
    };
    clipboard(text, event, success, error);
  };

  useEffect(() => {
    const hash = props.match.params.hash;
    if (hash) {
      setStatus(
        Codes.importFromHash(hash)
          .split('')
          .map(s => parseInt(s, 10))
      );
    } else if (localStorage.getItem(TOKEN_KEY)) {
      setStatus(
        Codes.importFromHash(localStorage.getItem(TOKEN_KEY))
          .split('')
          .map(s => parseInt(s, 10))
      );
    }
  }, [props, setStatus]);

  useEffect(() => {
    const fuzzFilter = item => {
      const hay = item.terms.toLowerCase();
      let i = 0;
      let n = -1;
      let l;
      const s = term.toLowerCase();
      for (; (l = s[i++]); ) if (!~(n = hay.indexOf(l, n + 1))) return false;
      return item.variations.length > 0;
    };

    let sortBase = [];

    switch (i18n.language) {
      case 'en':
        sortBase = sortEn;
        break;
      case 'ja':
        sortBase = sortJa;
        break;
      case 'zh-TW':
        sortBase = sortZhtw;
        break;
      default:
        sortBase = sortZhtw;
    }

    const resultsList = results.sort((a, b) => {
      return sortBase.indexOf(a.id) - sortBase.indexOf(b.id);
    });

    if (term === '' && filterType === 'all') {
      // Need deep copy the result array to re-render
      setData([...resultsList]);
    } else if (filterType === 'all') {
      setData(resultsList.filter(fuzzFilter));
    } else {
      setData(
        resultsList
          .map(item => {
            const factor = filterType === 'owned' ? 1 : 0;
            const variationsClone = [...item.variations];
            const filterResult = variationsClone.filter(
              variation => status[variation.idx] === factor
            );
            return {
              ...item,
              variations: filterResult,
            };
          })
          .filter(fuzzFilter)
      );
    }
  }, [setData, filterType, term, status, results, i18n.language]);

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }, []);

  const rowRenderer = ({ index, key, style }) => {
    const currentData = data[index];

    if (!currentData) {
      return <Row key="empty" style={style} />;
    }

    const row = (
      <Row>
        <Name>
          <div>{t(`collection.${currentData.id}`)}</div>
          <div>{currentData.name}</div>
        </Name>
        <Col>
          <div>{currentData.variations.length}</div>
        </Col>
        <Variations>
          {currentData.variations.map((variation, i) => (
            <VariationContainer
              key={i}
              onClick={() => onVariationClick(variation.idx)}
            >
              <VariationImg
                src={variation.content.image}
                isOwned={status[variation.idx]}
                alt={
                  variation.content.bodyColor_zh + ' ' + variation.content.name
                }
              />
              {currentData.variations.length > 1 && (
                <VariationToolTip>
                  {t(`variation.${variation.id}`)}
                </VariationToolTip>
              )}
            </VariationContainer>
          ))}
        </Variations>
      </Row>
    );

    return (
      <div key={key} style={style}>
        {index % 85 === 10 ? <>{row}</> : row}
      </div>
    );
  };

  // 廣告高度 ＋ 62(collection height + padding) + 20(margin of Adpanel)
  const getRowHeight = ({ index }) => {
    return index % 85 === 10 ? 182 : 70;
  };

  return (
    <div className="main">
      {loading && <Loading />}

      <Header
        shouldShowMyCollection={props.location.pathname !== '/'}
        total={status.length}
        owned={status.filter(it => it === 1).length}
        filterType={filterType}
        onSaveClick={onSaveClick}
        onShareClick={onShareClick}
        onSearchInputChange={onSearchInputChange}
        onFilterTypeChange={onFilterTypeChange}
      />

      <ScrollContainer>
        {data.length === 0 ? (
          <EmptyResult>
            <div>{t('search.emptyResult')}</div>
          </EmptyResult>
        ) : (
          <AutoSizer>
            {({ width, height }) => (
              <List
                height={height}
                width={width}
                overscanRowCount={10}
                rowCount={data.length + 1}
                rowRenderer={rowRenderer}
                rowHeight={getRowHeight}
              />
            )}
          </AutoSizer>
        )}
      </ScrollContainer>

      <Footer onLanguageChange={onLanguageChange} />

      <Dialog
        title={t('tool.copyTitle')}
        visible={isShareDialogVisible}
        customClass="dialog"
        closeOnClickModal
        onCancel={() => setIsShareDialogVisible(false)}
        onClose={() => setIsShareDialogVisible(false)}
        size="tiny"
      >
        <Dialog.Body>
          <span>{t('tool.copyInfo')}</span>
          <ShareBox onClick={e => handleClipboard(shareUrl, e)}>
            {shareUrl}
          </ShareBox>
        </Dialog.Body>
        <DialogFooter>
          <Button onClick={() => setIsShareDialogVisible(false)}>
            {t('button.close')}
          </Button>
          <Button type="primary" onClick={e => handleClipboard(shareUrl, e)}>
            {t('button.copy')}
          </Button>
        </DialogFooter>
      </Dialog>
    </div>
  );
};

App.propTypes = {
  location: PropTypes.shape({
    pathname: PropTypes.string,
  }).isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      hash: PropTypes.string,
    }),
  }).isRequired,
};

export default App;
